import kotlinx.serialization.Serializable

@Serializable
data class Hobby (
    var hobbyType: String = "",
    var age: Int = 0,
    var maxAge: Int = 0,
    var cost: Float = 0.0F,
    var point: Int = 0,
    var description: String = ""
)
{
    fun getAllHobbies(): List<Hobby> {
        return listOf(
            Hobby("cat",0,18,1000.0F,1,"Man blir lycklig och vlmende av katter"),
            Hobby("dog",0,15,2000.0F,2,"Hunden ger dig bde motion och en vn"),
            Hobby("horse",0,30,6000.0F,6,"Hsten ger dig vnner, socialsammanhang, vlmende och du blir stark"),
            Hobby("party",0,1,3000.0F,3,"Partyt livar upp och ger dig mnga vnner"),
            Hobby("car",0,10,5000.0F,5,"Bilen ger dig bde frihet och bekvmlighet"),
            Hobby("bike",0,10,3000.0F,3,"Motorcykeln ger dig en frihetsknsla och snabbhet"),
            Hobby("boat",0,10,1000.0F,1,"Bten ger dig en hrlig knsla p bljan det bl"),

            Hobby("friend",0,0,0.0F,1,"Vnner ger dig gemenskap och vlmende"),
            Hobby("strong",0,0,0.0F,1,"Trning gr dig stark, vlmende och pigg"),
            Hobby("fish",0,0,0.0F,1,"En fisketur p bljan det bl ger frisk luft och vlmende"),
            Hobby("love",0,0,0.0F,1,"Sambon betalar halva hyran!"),
            Hobby("walk",0,0,0.0F,1,"Att bara f vandra sjlv ger insikt, motion och vlmende")
        )
    }

    fun costHobby(): Float {
        var cost = 0.0F

        for (hobby in this.getAllHobbies()) {
            if (this.hobbyType == hobby.hobbyType)
                cost = this.cost
        }

        return cost
    }

    fun getHobby(hobbyType: String): Hobby {
        var hobby = Hobby("")
        val allHobbies = hobby.getAllHobbies()

        for (next in allHobbies) {
            if (hobbyType == next.hobbyType)
                hobby = next
        }

        return hobby
    }
}