import kotlinx.serialization.Serializable

@Serializable
data class Life ( val personId: Int) {
    var person = Person(personId)
    var firstStep = true
    var age = 0
    var parent = Parent(personId)
    var employee = Employee(1)
    var professionId: Int = 0
    var accountPension = Account(1, "pensionskonto")
    var accountSalary = Account(2, "lnekonto")
    var accountDepot = Account(3, "dep")
    var accountTax = Account(4, "skatt")
    var accountNoAkassa = Account(5, "noakassa")
    var accountWorkPension = Account(6, "jobbpensionskonto")
    var accountTaxPension = Account(7, "skattpensionskonto")
    var messageList: List<Message> = emptyList()
    var lastMessageId: Int = 0
    var isPandemi = false
    var isBoom = false
    var isRecession = false
    var isQuestion = false
    var questionMessageId = 0
    var isNewProfession = false
    var professionMessageId = 0
    var isHouseQuestion = false
    var houseQuestionMessageId = 0
    var firstSalary: Float = 0.0F
    //var urlPension = "https://www.pensionsmyndigheten.se/forsta-din-pension/sa-fungerar-pensionen/sa-tjanar-du-in-till-din-pension"
    var countPoint: Int = 0
    var currentAmount: Float = 0.0F
    var currentCost: Float = 0.0F
    var sumPensionMonth: Float = 0.0F
    var luck: String = ""
    var luckPoint: Int = 0

    fun showPensionLife(messageList: List<Message>, messageId: Int): List<Message> {
        var storyList = messageList
        var storyId = messageId

        storyId += 1
        val message = Message(
            storyId,
            "----------------------- Du r ${this.age} r ------------------------",
            "deepskyblue",
            ""
        )
        storyList = storyList.plus(message)

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Du har nu gtt i pension, det r nu livet brjar.",
                "",
                ""
            )
        )

        if (this.parent.countBabies > 0) {
            this.countPoint += this.parent.countBabies * 3

            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Nu kan du umgs med barn och barnbarn, lyckopong: ${this.countPoint} ",
                    "",
                    ""
                )
            )
        }

        if (this.person.isLove) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Din livskrlek finns med dig .",
                    "",
                    "blinking"
                )
            )
            this.countPoint += 1
        }

        if (this.person.houses.size > 1) {
            if (this.person.houses[this.person.houses.size - 1].houseMonthPayment > 0.0F) {
                storyId += 1
                storyList = storyList.plus(
                    Message(
                        storyId,
                        "Du fann dig till ro i ditt bo, ${this.person.houses[this.person.houses.size - 1].description}",
                        "",
                        ""
                    )
                )
                this.countPoint += 1
            } else {
                storyId += 1
                storyList = storyList.plus(
                    Message(
                        storyId,
                        "Du lnar en soffa att bo p.",
                        "",
                        ""
                    )
                )
            }
        }

        when (this.person.professions[0].objectType) {
            "salary" -> {
                // Question(4,"Maxa lnen", "ln", "salary"),

                if (this.employee.currentSalary >= 100000) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa lnen: ${this.employee.currentSalary.toInt().formatDecimalSeparator()}, vilket du gjorde!",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa lnen: ${this.employee.currentSalary.toInt().formatDecimalSeparator()}, du kom inte helt i ml.",
                            "",
                            ""
                        )
                    )
                }
            }

            "pension" -> {
                // Question(5, "Maxa pensionen", "pension", "pension"),

                if (this.accountPension.amount >= 5000000) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa pensionen: ${
                                this.accountPension.amount.toInt().formatDecimalSeparator()
                            }, vilket du gjorde!",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa pensionen: ${
                                this.accountPension.amount.toInt().formatDecimalSeparator()
                            }, du kom inte helt i ml.",
                            "",
                            ""
                        )
                    )
                }
            }

            "adventure" -> {
                // Question(0,"Maxa spnningen", "spnning", "adventure"),

                if (this.person.countSickMonth >= 12) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa spnningen, vilket du gjorde med antal sjukmnader: ${this.person.countSickMonth}.",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa spnningen, du kom inte helt i ml d du verkar oskadd :).",
                            "",
                            ""
                        )
                    )
                }
            }

            "vacation" -> {
                // Question(2, "Maxa semesterdagarna", "semester", "vacation"),

                if (this.age <= 60) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa semestern, vilket du gjorde med att g i pension tidigt.",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att maxa semestern, du kom inte helt i ml d du gr i pension sent.",
                            "",
                            ""
                        )
                    )
                }
            }

            "family" -> {
                // Question(1, "Gra samhllsnytta och skillnad", "familj", "family",),

                if (this.employee.currentSalary <= 45000) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Du vill gra samhllsnytta och skillnad, du gick p ditt kall ven om lnen var lg: ${
                                this.employee.currentSalary.toInt().formatDecimalSeparator()
                            }",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Du vill gra samhllsnytta och skillnad, du gick inte helt p ditt kall och accepterade lg ln",
                            "",
                            ""
                        )
                    )
                }
            }

            "chilla" -> {
                // Question(3, "Chilla", "chilla", "chilla"),

                if ((this.accountSalary.amount + this.accountDepot.amount) < 0) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att chilla, som du klarade det och tjnade inte s mycket: ${
                                this.accountSalary.amount.toInt().formatDecimalSeparator()
                            }",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att chilla, du kom inte helt i ml d du verkar ha jobbat :)",
                            "",
                            ""
                        )
                    )
                }
            }

            "fun" -> {
                // Question(6, "Bara ha kul", "kul", "fun")

                if (this.person.countSickMonth <= 1) {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Ditt ml var att ha kul. Livet var s kul och du var sllan sjuk.",
                            "",
                            "blinking"
                        )
                    )
                    this.countPoint += 5
                } else {
                    storyId += 1
                    storyList = storyList.plus(
                        Message(
                            storyId,
                            "Livet var inte alltid s kul, du blev sjuk i ${this.person.countSickMonth} mnader",
                            "",
                            ""
                        )
                    )
                }
            }
        }

        if (this.person.professions.size <= 1) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du stannade p samma arbetsplats hela livet.",
                    "",
                    ""
                )
            )
        } else {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du fann den arbetsplats som frstod ditt vrde.",
                    "",
                    ""
                )
            )
            this.countPoint += 1
        }

        //Summa konton
        currentAmount = this.accountDepot.amount + this.accountSalary.amount
        //+ Summa pension - skatt
        currentAmount += this.accountPension.amount - (this.accountPension.amount * 0.30F)

        //Summa kostnader per mnad + kostnad mat
        currentCost = if (this.person.isLove)
            (this.person.house.houseMonthPayment / 2) + (person.costHobbies() / 12) + 3000
        else
            this.person.house.houseMonthPayment + (person.costHobbies() / 12) + 3000

        sumPensionMonth = currentAmount / currentCost

        if (currentAmount <= 0.0F) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du ligger back: ${currentAmount.toInt().formatDecimalSeparator()} SEK.",
                    "",
                    ""
                )
            )
        } else {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du har sparat totalt (-skatt): ${currentAmount.toInt().formatDecimalSeparator()} SEK",
                    "",
                    ""
                )
            )
        }
        if ((this.accountNoAkassa.amount - this.accountSalary.amount) < 0) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Med a-kassa (inkomstfrskring och tillggsfrskring) tjnade du: ${
                        (this.accountSalary.amount - this.accountNoAkassa.amount).toInt().formatDecimalSeparator() 
                    } SEK",
                    "",
                    ""
                )
            )
        }

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Totalt i kostnader per mnad: ${currentCost.toInt().formatDecimalSeparator()} SEK",
                "",
                ""
            )
        )

        if (sumPensionMonth > 0.0F) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Det gr att du klara dig tills du r: ${(this.age + (sumPensionMonth / 12).toInt()).formatDecimalSeparator()} r",
                    "",
                    ""
                )
            )
        } else {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du fr jobba ngra r till.",
                    "",
                    ""
                )
            )
        }

        //Ett pong fr varje r du klara
        if ((this.age + (sumPensionMonth / 12)) > 100) {
            this.countPoint += (100 - this.age)
        } else {
            this.countPoint += (sumPensionMonth / 12).toInt()
        }

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Lycka r nr pengarna rcker livet ut, lyckopong: ${this.countPoint}",
                "",
                ""
            )
        )

        //Berkna antal lyckliga pong
        this.countPoint += this.luckPoint

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Din sammanlagda lyckopong: ${this.countPoint}",
                "",
                "blinking"
            )
        )

        if (this.countPoint > 50) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du har haft ett vldigt lyckligt arbetsliv.",
                    "",
                    "blinking"
                )
            )
        } else if (this.countPoint > 25) {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du har haft ett lyckligt arbetsliv.",
                    "",
                    "blinking"
                )
            )
        } else if (this.accountSalary.amount < 0.0F){
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du behver nog jobba ett tag till.",
                    "",
                    "blinking"
                )
            )
        } else {
            storyId += 1
            storyList = storyList.plus(
                Message(
                    storyId,
                    "Du har pengar, men blev du lycklig?",
                    "",
                    "blinking"
                )
            )
        }

        return storyList
    }

    fun showLifeValue(messageList: List<Message>, messageId: Int): List<Message> {
        var storyList = messageList
        var storyId = messageId

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "------------------------------------------------------------------------",
                "deepskyblue",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Jag vill bertta att du inte r vrd ngot om du inte r p rtt plats.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Om du inte r uppskattad, var inte arg, det betyder att du r p fel stlle.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Stanna inte p en plats dr ingen ser ditt vrde.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "------------------------------------------------------------------------",
                "deepskyblue",
                ""
            )
        )


        return storyList
    }

    fun showRichLife(messageList: List<Message>, messageId: Int): List<Message> {
        var storyList = messageList
        var storyId = messageId

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "------------------------------------------------------------------------",
                "deepskyblue",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Oavsett om kr en dyr eller billig bil, r vgen och strckan densamma.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Oavsett om huset vi bor i r 100 eller 1000 kvm r ensamheten densamma.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Oavsett om du flyger frsta klass eller ekonomiklass, gr du under om planet kraschar.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "Du kan anlita ngon som tjnar pengar t dig, men inte ngon som dr t dig.",
                "",
                ""
            )
        )

        storyId += 1
        storyList = storyList.plus(
            Message(
                storyId,
                "------------------------------------------------------------------------",
                "deepskyblue",
                ""
            )
        )

        return storyList
    }
}
