import kotlinx.serialization.Serializable

@Serializable
data class View(
    val id: Int,
    val viewType: String = "init",
    val questions: List<Question> = emptyList(),
    val title: String = "",
    val buttonText: String = "",
    val nextViewType: String = "action"
) {

    fun getViewList(): List<View> {
        val question = Question(0)
        //val unionQuestions: List<Question> = question.getQuestionList("union")
        val goalQuestions: List<Question> = question.getQuestionList("goal")
        val luckQuestions: List<Question> = question.getQuestionList("luck")
        val professionQuestions: List<Question> = question.getQuestionList("profession")
        val houseQuestions: List<Question> = question.getQuestionList("house")

        return listOf(
            View(0, "init", emptyList(), "Ditt arbetsliv brjar hr:","Nsta steg", "action"),
            View(1, "action", goalQuestions,"Vilket ml har du med arbetslivet?", "Vlj yrke", "profession"),
            View(2, "start", emptyList(),"Ditt arbetsliv har startat!", "G vidare i arbetslivet", "reload"),
            View(3, "luck", luckQuestions,"Vad gr dig lycklig?", "Starta", "start"),
            View(4, "profession", professionQuestions,"Vilket yrke vljer du?", "Nsta", "luck"),
            View(5, "reload", emptyList(),"Mitt i livet", "Traska p", "pension"),
            View(6, "pension", emptyList(),"Nu startar pensionen", "Pensionr", "slut"),
            View(7, "question", professionQuestions,"Vilket yrke vljer du?", "G vidare till nya jobbet", "reload"),
            View(8, "depressed", luckQuestions,"Vad gr dig lycklig?", "G vidare lycklig", "reload"),
            View(9, "house", houseQuestions,"Var vill du bo?", "G vidare", "reload")
        )
    }
    fun getNextView(): View {
        var view = View(0)
        val allViews = view.getViewList()

        for (next in allViews) {
            if (this.nextViewType == next.viewType)
                view = next
        }

        return view
    }

    fun getNewView(viewType: String): View {
        var view = View(0)
        val allViews = view.getViewList()

        for (next in allViews) {
            if (viewType == next.viewType)
                view = next
        }

        return view
    }
}